
body {
    background: #27103e;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    padding-top: 100px;
}
.sketch-panel {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 80%;
    max-width: 700px;
    margin: 0 auto;
    height: 25px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background: #403a46;
    padding: 15px 35px;
    border-radius: 0 0 10px 10px;
    color: #fff;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.svg-sketch {
    max-width: 1000px;
    margin: 0 auto;
    -webkit-box-shadow: rgba(0, 0, 0, 0.1) 0 0 10px;
            box-shadow: rgba(0, 0, 0, 0.1) 0 0 10px;
    background: #fff;
}
.svg-sketch-wrap,
.svg-sketch svg {
    width: 100%;
}



